import React from 'react'

const SocialDetails = () => {
  return (
    <>
      <div class="offcanvas offcanvas-end applyLeaveCanvas" tabindex="-1" id="socialDetails">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Profile Details</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body pt-0 mt-3">
            <div class="formPanel">
                <div class="detailMain">
                    <div class="personalDetail d-flex align-items-center">
                        <a href="#" class="active"><img src="images/previousIcon-blue.svg" width="24" alt=""/></a>
                        <div class="personalLeft ms-2">
                            <span>Social Details</span>
                            <p>Lorem Ipsum is simply dummy text of the printing </p>
                            <strong>Status : <b class="text-success">4 of 4</b> </strong>
                        </div>
                    </div>
                    <div class="personalDetail d-flex align-items-center">
                        <div class="personalLeft border-0 me-2 text-end">
                            <span>NEXT </span>
                            <p class="m-0">Reference Details</p>
                        </div>
                        <a href="#" class="active"><img src="images/nextIconblue.svg" width="24" alt=""/></a>
                    </div>
                </div>
                <div class="mb-1">
                    <div class="row p-2">
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label htmlFor="">Emp User ID</label>
                                <input type="text" name="" id="" class="form-control" placeholder="1234" readonly/>
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label htmlFor="">Emp Linkdin ID</label>
                                <input type="text" name="" id="" class="form-control" placeholder="None" readonly/>
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label htmlFor="">Emp Facebook ID</label>
                                <input type="text" name="" id="" class="form-control" placeholder="None" readonly/>
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label htmlFor="">Emp Twitter ID</label>
                                <input type="text" name="" id="" class="form-control" placeholder="None" readonly/>
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label htmlFor="">Emp Instagram ID</label>
                                <input type="text" name="" id="" class="form-control" placeholder="None" readonly/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="offcanvas-footer">
            <div class="d-flex align-items-center justify-content-end">
                <a href="#" class="btn btn-secondary"> Back</a>
            </div>
        </div>
    </div>
    </>
  )
}

export default SocialDetails
