import React from 'react'
import Lottieplayer from '../commanComponent/Lottieplayer'
import businesshiring from "../../images/business-hiring.json"
import Lottie from 'react-lottie-player'

const ProfileEditDocCard = () => {
    return (
        <>
            <div class="cardBox mb-3 createNewMrf">
                <div class="row align-items-center">
                    <div class="col-md-9 col-xl-10">
                        <div class="row align-items-center">
                            <div class="col-md-3">

                                <Lottie
                                    loop
                                    // src="../images/star-icon.json"
                                    animationData={businesshiring}
                                    background="transparent"
                                    speed="1"
                                    play
                                    style={{ width: "100%", height: "124px" }}
                                    autoPlay
                                />



                            </div>
                            <div class="col-md-9">
                                <div class="cardHead pb-0">
                                    <h3>Edit <span>Documents</span></h3>
                                    <p class="mb-0">Lorem Ipsum is simply dummy text of the printing and typesetting
                                        industry. Lorem Ipsum has been the industry's</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-3 col-xl-2">
                        <a href="#profileDetails" data-bs-toggle="offcanvas" class="btn btn-primary gradientBg w-100">Edit
                            Documents</a>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProfileEditDocCard
