import React, { useEffect, useState } from 'react'
import { ProfileInfo, ProfileEditDocCard, ProfileDetailCards } from '.'
import {
  ProfileDetail,
  FamilyDetail,
  EducationDetail,
  WorkExpDetail,
  SocialDetail,
  ReffrenceDetail,
  PolicyDetails,
  IdCard,
  DocumentDetail,
  BankDetail,
} from '../modal/profile'
import { useDispatch, useSelector } from 'react-redux'
import { employeeActions } from '../../core/resource/actions'
const Profile = () => {
  const dispatch = useDispatch()
  const {
    getProfilePending,
    getProfileRes,
    getProfileError,
  } = useSelector(state => state.employee)
  const [profileData, setProfileData] = useState()


  useEffect(() => {
    getProfileData()
  }, [])
  /* Get Employee Profile Data*/
  const getProfileData = () => {
    dispatch(employeeActions.getProfileRequest())
  }

  useEffect(() => {
    if (!getProfilePending) {
      if (getProfileError) {
        return;
      }
      if (getProfileRes) {
        if (getProfileRes?.code === 200) {
          setProfileData(getProfileRes?.data)
        }
      }
    }
  }, [
    getProfileRes,
    getProfileError,
    getProfilePending
  ]);

  return (
    <>
      <ProfileInfo 
      profileData ={profileData}
      />
      <ProfileEditDocCard />
      <ProfileDetailCards />
      <ProfileDetail />
      <FamilyDetail />
      <EducationDetail />
      <WorkExpDetail />
      <SocialDetail />
      <ReffrenceDetail />
      <PolicyDetails />
      <DocumentDetail />
      <BankDetail />
      <IdCard />


    </>
  )
}

export default Profile
