import React from 'react'

const BankDetail = () => {
  return (
    <>
     <div className="offcanvas offcanvas-end applyLeaveCanvas" tabindex="-1" id="bankDetails">
        <div className="offcanvas-header">
            <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Profile Details</h5>
            <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div className="offcanvas-body pt-0 mt-3">
            <div className="formPanel">
                <div className="detailMain">
                    <div className="personalDetail d-flex align-items-center">
                        <a href="#" className="active"><img src="images/previousIcon-blue.svg" width="24" alt=""/></a>
                        <div className="personalLeft ms-2">
                            <span>Bank Details</span>
                            <p>Lorem Ipsum is simply dummy text of the printing </p>
                            <strong>Status : <b className="text-success">4 of 4</b> </strong>
                        </div>
                    </div>
                    <div className="personalDetail d-flex align-items-center">
                        <div className="personalLeft border-0 me-2 text-end">
                            <span>NEXT </span>
                            <p className="m-0">Social Details</p>
                        </div>
                        <a href="#" className="active"><img src="images/nextIconblue.svg" width="24" alt=""/></a>
                    </div>
                </div>
                <div className="mb-1">
                    <div className="row p-2">
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Emp User ID</label>
                                <input type="text" name="" id="" className="form-control" placeholder="1234" readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Emp Bank Account Holder Name</label>
                                <input type="text" name="" id="" className="form-control" placeholder="Nikita Prajapat"
                                    readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Emp Bnak Name</label>
                                <input type="text" name="" id="" className="form-control" placeholder="Bank of Baroda"
                                    readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Emp Bank Account Number</label>
                                <input type="text" name="" id="" className="form-control" placeholder="12345678910230"
                                    readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Emp IFSC Code</label>
                                <input type="text" name="" id="" className="form-control" placeholder="BARB5274RJAI"
                                    readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">EMP Nominee</label>
                                <input type="text" name="" id="" className="form-control" placeholder="No" readonly/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label htmlFor="">Payment Mode</label>
                                <input type="text" name="" id="" className="form-control" placeholder="-" readonly/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div className="offcanvas-footer">
            <div className="d-flex align-items-center justify-content-end">
                <a href="#" className="btn btn-secondary"> Back</a>
            </div>
        </div>
    </div>
    </>
  )
}

export default BankDetail
